@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-style: $style;
    font-weight: #{$weight};
    font-family: "#{$font-family}";

    font-display: swap;
    src: url("../fonts/#{$url}.woff2") format("woff2"),
    url("../fonts/#{$url}.woff") format("woff");
  }
}

@include font-face("Red Hat Display",  "RedHatDisplay-Regular", 400, normal);
@include font-face("Red Hat Display",  "RedHatDisplay-Medium", 500, normal);
@include font-face("Red Hat Display",  "RedHatDisplay-SemiBold", 600, normal);
@include font-face("Red Hat Display",  "RedHatDisplay-Bold", 700, normal);
@include font-face("Red Hat Display",  "RedHatDisplay-ExtraBold", 800, normal);
@include font-face("Red Hat Display",  "RedHatDisplay-Black", 900, normal);


